<template>
  <page-box>
    <Header title="About me" img="/default/img3.png"></Header>
    <div class="main-area">
      <loading class="article-loading" v-if="!isShow"></loading>
      <transition name="fade">
        <div class="about">
          <page-padding class="about-body markdown-body" v-html="compiledMarkdown"></page-padding>
        </div>
      </transition>
    </div>
  </page-box>
</template>

<script>
import Header from '@/components/Header'
import Loading from '@/components/Loading'
import pageBox from '@/components/PageBox'
import pagePadding from '@/components/PagePadding'

import data from '@/data'
import tools from '@/utils/public'

import hljs from 'highlight.js'
import marked from 'marked'

export default {
  name: 'AboutPage',
  components: {
    Header, Loading, pageBox, pagePadding
  },
  data() {
    return {
      isShow: false,
      aboutInfo: ''
    }
  },
  computed: {
    compiledMarkdown() {
      return marked(this.aboutInfo)
    }
  },
  mounted() {
    this.setMarkDown();

    this.getInfo();
  },
  methods: {
    getInfo() {
      data.getAboutInfo().then(data => {
        this.isShow = true;
        
        this.aboutInfo = data;
      })
    },
    setMarkDown () {
      marked.setOptions({
        renderer: new marked.Renderer(),
        gfm: true,
        tables: true,
        breaks: false,
        pedantic: false,
        sanitize: true,
        smartLists: true,
        smartypants: false,
        highlight: function(code) {
          return hljs.highlightAuto(code).value
        }
      });
    }
  }

}
</script>

<style lang="scss" scoped>
.main-area {
  min-height: 400px;

  .article-loading {
    margin: 40px 0;
  }

  .fade-enter-active, .fade-leave-active{
    transition: opacity 1s;
  }
  .fade-enter, .fade-leave-to {           
    opacity: 0;
  }

}
</style>
